	import {mapMutations} from 'vuex';
	import Utils from '../../common/utils.js'
	export default {
		data() {
			return {
				
				// isCanUse: uni.getStorageSync('isCanUse')||true//默认为true










			};
		},
		onLoad() {
			console.log(1231)
		},
		onShow() {

			this.getAuth();

		},
		methods: {
			...mapMutations(['login']),
















































































































			getAuth() {
				let _this = this;
				uni.getSetting({
					success(res) {
						if (res.authSetting['scope.userInfo']) {
							uni.login({
								provider: 'weixin',
								success(res1) {
									uni.getUserInfo({
										provider: 'weixin',
										success(res) {
											console.log(res)
											_this.userLogin(res.rawData, res1.code,res.encryptedData,res.iv);
										}
									})
								}
							})
						} else {
							// _this.checkAuth();
						}
					}
				})
			},
			wxGetUserInfo(res) {
				console.log(res)
				let _this = this;
				if (res.detail.errMsg == "getUserInfo:ok") {
					uni.login({
						provider: 'weixin',
						success(res1) {
							_this.userLogin(res.detail.rawData, res1.code,res.detail.encryptedData,res.detail.iv);
						}
					})

				}
			},
			checkAuth() {
				let _this = this;
				uni.showModal({
					content: '请开启微信授权登陆',
					cancelText: '取消',
					confirmText: '授权',
					success(res) {
						if (res.confirm) {
							console.log('用户点击了确认')
						} else {
							console.log('用户点击了取消')
						}
					}
				});
			},
			

			userLogin(userInfo, code='',encryptedData='',iv='') {
				
				let _this = this;
				_this.$http.request({
					method: 'POST',
					url: _this.$api.AuthLoginByWeixin,
					data: {
						code: code,
						userInfo: JSON.parse(userInfo),
						encryptedData:encryptedData,
						iv:iv,
						recommendId: ''
					}
				}).then(res => {
					//本地存储token
					if(res.errno != '-1'){
						this.login(res.data);
						uni.setStorageSync("X-MCB-Token", res.data.token);
						uni.switchTab({
							"url": "/pages/home/index/index"
						});
					}
					
				})
			
			
			}

		}

	}
